import React, { useState } from "react";

export default function TextForm(props) {
  const changeText = (e) => {
    console.log(e.target.value);
    console.log("change in form");
    setState(e.target.value);
  };

  const convertUpper = () => {
    console.log("changes to upper");
    setState(text.toUpperCase());
    props.showAlert("Converted in UpperCase","success")
  };

  const convertLower = () => {
    console.log("changes to lower");
    setState(text.toLowerCase());
    props.showAlert("Converted in LowerCase","success")
  };

  const XtraLine = () => {
    console.log("Extra Space Remover");
    let newText = text.split(/[ ]+/);
    setState(newText.join(" "));
    props.showAlert("Extra Line Removed","success")
  };

  const download = () => {
    console.log("Request to Download the text");
    let downloadButton = document.createElement("a");
    downloadButton.setAttribute(
      "href",
      "data:text/plain;charset=utf-8," + encodeURIComponent(text)
    );
    downloadButton.setAttribute("download", "text.txt");
    document.body.append(downloadButton);
    downloadButton.click();
    document.body.removeChild(downloadButton);
  };

  const clear = () => {
    console.log("Clear Text");
    setState("");
    props.showAlert("Text Cleared","success")
  };

  const [text, setState] = useState("");
  return (
    <>
      <div>
      <h1
          className={`text-center m-3 text-${
            props.mode === "light" ? "dark" : "light"
          }`}
        >
          Enter Text to Analysis
        </h1>
        <div className="my-3">
          <textarea
            placeholder="Enter Text"
            onChange={changeText}
            className={`form-control border-primary p-2 bg-${
              props.mode === "light" ? "light" : "dark"
            } text-${props.mode === "light" ? "dark" : "light"}`}
            value={text}
            id="textt"
            rows="5"
          ></textarea>
        </div>
        <button className="btn btn-primary  m-1" onClick={convertUpper}>
          UpperCase
        </button>
        <button className="btn btn-primary m-1" onClick={convertLower}>
          LowerCase
        </button>
        <button className="btn btn-primary m-1" onClick={clear}>
          ClearText
        </button>
        <button className="btn btn-primary m-1" onClick={download}>
          DownloadText
        </button>
        <button className="btn btn-primary m-1" onClick={() => {navigator.clipboard.writeText(text); props.showAlert("Text Copied","success")} }>
          CopyText
        </button>
        <button className="btn btn-primary m-1" onClick={XtraLine}>
          Spaceremove
        </button>
      </div>
      <div className="container">
        <h2
          className={`my-3 text-center text-${
            props.mode === "light" ? "dark" : "light"
          }`}
        >
          Your Text Summary
        </h2>
        <div className={`text-${props.mode === "light" ? "dark" : "light"}`}>
          Words : {text.split(" ").length} <br />
          Characters : {text.length}
          <br /> Time Required to read your Text : {0.008 * text.split(" ")}
        </div>
        <h2
          className={`my-3 text-center text-${
            props.mode === "light" ? "dark" : "light"
          }`}
        >
          Your Text Preview
        </h2>
        <div className={`text-${
            props.mode === "light" ? "dark" : "light"
          }`}>
        {text}
        </div>
      </div>
    </>
  );
}
