import React from "react";
import PropTypes from "prop-types";

export default function NavBar(props) {
  return (
    <div>
      <nav
        className={`navbar navbar-expand-lg text-${props.mode} navbar-${props.mode} bg-${props.mode}`}
      >
        <div className="container-fluid">
          <a className="navbar-brand" href="#">
            {props.title}
          </a>
            <div className="form-check form-switch d-flex m-1 justify-content-end">
              <input
                className="form-check-input "
                onClick={props.toggle}
                type="checkbox"
                id="flexSwitchCheckDefault"
              />
            </div>
        </div>
      </nav>
    </div>
  );
}

NavBar.propTypes = {
  title: PropTypes.string,
};

NavBar.defaultProps = {
  title: "DefaultTitle",
};
